@import "./variables.scss";

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  font-family: inherit;
}

body {
  box-sizing: border-box;
  height: 100vh;
  font-family: "Nunito Sans", sans-serif;
}

ul {
  list-style: none;
}

a:link,
a:visited {
  color: inherit;
  text-decoration: none;
}
